import { useAppSelector } from "app/hooks"
import s from "./HistoryComponent.module.scss"
import { selectUser } from "features/user/userSlice"
import { ROUTES } from "app/routes"
import { Link } from "react-router-dom"

export const History = () => {
    //for i 
    const user = useAppSelector(selectUser)
    return (
        <div className={s["history-orders"]}>
            <h1 className={`${s.cabinet__title} ${s.title}`}> </h1>
            {
                user?.history && user.history.length > 0 ?
                    <div>
                        {
                            user?.history.map((item, index) => {
                                return (
                                    <div key={index} className={s["history-orders__wrapper"]}>
                                        <div>
                                            <h2 className={s["history-orders__header"]}> {user.orderNumber[index]}</h2>
                                            <h2 className={s["history-orders__title"]}>:</h2>
                                            <ul className={s["history-orders__list"]}>
                                                <li>: {item.dateInfo}</li>
                                                <li>: {item.timeInfo}</li>
                                            </ul>
                                            <h2 className={s["history-orders__title"]}>:</h2>
                                            <ul className={s["history-orders__list"]}>
                                                <li>': {item.first_name}</li>
                                                <li>: {item.last_name}</li>
                                                <li>: {item.phone_number}</li>
                                                <li> : {item.email}</li>
                                            </ul>
                                            <h2 className={s["history-orders__title"]}>  :</h2>
                                            <ul className={s["history-orders__list"]}>
                                                <li>: {item.city}</li>
                                                <li> : {item.delivery.postamt !== undefined ? "    " : item.delivery.postOffice !== undefined ? "    ." : item.delivery.courier !== undefined && !item.delivery.courier.includes("undefined") ? "'   " : ""}</li>
                                                <li>: {item.delivery.postamt !== undefined ? item.delivery.postamt : item.delivery.postOffice !== undefined ? item.delivery.postOffice : item.delivery.courier?.includes("undefined") ? "" : item.delivery.courier}</li>
                                                <li> : {item.activePay}</li>
                                            </ul>
                                        </div>
                                        <div>
                                            <h2 className={s["history-orders__header"]}></h2>
                                            <h2 className={s["history-orders__title"]}>:</h2>
                                            <div>
                                                {item.cart.map((cartItem) => {
                                                    return (
                                                        <ul className={s["history-orders__products"]}>
                                                            <li className={s["history-orders__box"]}>
                                                                <Link to={`${ROUTES.product}/${cartItem.id}`}>
                                                                    <img src={cartItem.img} />
                                                                </Link>
                                                                <p>{cartItem.title}</p>
                                                            </li>
                                                            <li>  : {cartItem.price}</li>
                                                            <li>: {cartItem.count}</li>
                                                        </ul>
                                                    )
                                                })}
                                            </div>
                                            <div className={s["history-orders__footer"]}>
                                                <p> : {item.totalPrice}</p>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                    :
                    <p>  </p>
            }
        </div>
    )
}